"use client";

import { useState } from 'react';
import Image from 'next/image';
import Link from 'next/link';

const Blog = () => {
  const [activeCategory, setActiveCategory] = useState('all');

  const articles = [
    {
      id: 1,
      title: "Building Scalable Web Applications with Next.js",
      excerpt: "Learn how to build high-performance web applications using Next.js and modern best practices.",
      category: "web-development",
      date: "March 15, 2024",
      readTime: "8 min read",
      image: "/blog1.jpg",
      tags: ["Next.js", "React", "Performance", "Web Development"]
    },
    {
      id: 2,
      title: "Data Analysis with Python: A Comprehensive Guide",
      excerpt: "Master the art of data analysis using Python's powerful libraries and tools.",
      category: "data-science",
      date: "March 10, 2024",
      readTime: "10 min read",
      image: "/blog2.jpg",
      tags: ["Python", "Data Analysis", "Pandas", "Data Science"]
    },
    {
      id: 3,
      title: "Machine Learning in Production: Best Practices",
      excerpt: "Essential tips and practices for deploying machine learning models in production environments.",
      category: "machine-learning",
      date: "March 5, 2024",
      readTime: "12 min read",
      image: "/blog3.jpg",
      tags: ["Machine Learning", "MLOps", "Deployment", "AI"]
    }
  ];

  const categories = [
    { id: 'all', name: 'All Articles' },
    { id: 'web-development', name: 'Web Development' },
    { id: 'data-science', name: 'Data Science' },
    { id: 'machine-learning', name: 'Machine Learning' }
  ];

  const filteredArticles = activeCategory === 'all'
    ? articles
    : articles.filter(article => article.category === activeCategory);

  return (
    <section id="blog" className="py-20 bg-gray-50 dark:bg-gray-900">
      <div className="container mx-auto px-4">
        <h2 className="text-3xl md:text-4xl font-bold text-center mb-4 text-gray-700 dark:text-white">
          Latest Articles
        </h2>
        <p className="text-center text-gray-500 dark:text-gray-400 mb-12 max-w-2xl mx-auto">
          Insights, tutorials, and thoughts on technology and development
        </p>

        {/* Category Navigation */}
        <div className="flex flex-wrap justify-center gap-4 mb-12">
          {categories.map((category) => (
            <button
              key={category.id}
              onClick={() => setActiveCategory(category.id)}
              className={`px-6 py-2 rounded-full transition-all duration-300 ${
                activeCategory === category.id
                  ? 'bg-primary text-white shadow-lg'
                  : 'bg-white dark:bg-gray-800 text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
              }`}
            >
              {category.name}
            </button>
          ))}
        </div>

        {/* Articles Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8 max-w-6xl mx-auto">
          {filteredArticles.map((article) => (
            <article
              key={article.id}
              className="bg-white dark:bg-gray-800 rounded-xl overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300"
            >
              <div className="relative h-48">
                <Image
                  src={article.image}
                  alt={article.title}
                  fill
                  className="object-cover"
                />
              </div>
              <div className="p-6">
                <div className="flex items-center text-sm text-gray-500 dark:text-gray-400 mb-4">
                  <span>{article.date}</span>
                  <span className="mx-2">‚Ä¢</span>
                  <span>{article.readTime}</span>
                </div>
                <h3 className="text-xl font-bold text-gray-700 dark:text-white mb-3">
                  {article.title}
                </h3>
                <p className="text-gray-500 dark:text-gray-400 mb-4">
                  {article.excerpt}
                </p>
                <div className="flex flex-wrap gap-2 mb-4">
                  {article.tags.map((tag, index) => (
                    <span
                      key={index}
                      className="px-3 py-1 bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded-full text-sm"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
                <Link
                  href={`/blog/${article.id}`}
                  className="text-primary hover:text-primary/80 font-medium inline-flex items-center"
                >
                  Read More
                  <svg
                    className="w-4 h-4 ml-2"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M9 5l7 7-7 7"
                    />
                  </svg>
                </Link>
              </div>
            </article>
          ))}
        </div>

        {/* View All Button */}
        <div className="text-center mt-12">
          <Link
            href="/blog"
            className="inline-flex items-center px-6 py-3 bg-primary text-white rounded-lg hover:bg-primary/90 transition-colors"
          >
            View All Articles
            <svg
              className="w-4 h-4 ml-2"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M9 5l7 7-7 7"
              />
            </svg>
          </Link>
        </div>
      </div>
    </section>
  );
};

export default Blog; 